
import { useState, useEffect, useRef } from 'react';
import { Redirect,Link } from 'react-router-dom';
import FormElement from './FormElement';
// import FormElementCategory from './FormElementCategory';
import { FormContext } from './FormContext';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
import axios from 'axios'
// import CheckSurveyTypeResponse from './CheckSurveyTypeResponse';
import Form from "react-validation/build/form";
// import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
// import SurveyAttachment from './SurveyAttachment';
import authService from "../services/auth.service";
// import { render } from 'react-dom';
import { useModal } from 'react-hooks-use-modal';
// import {useUpdateEffect} from 'react-use';


//hey i will use react hook here
const CompletePensiondeduction = (props) => {

  
  const form = useRef();
  const checkBtn = useRef();
  const [Get_Survey_Details_Pension, Set_Survey_Details_pension] = useState({});
  const [successful, setSuccessful] = useState(false);
  const [title, setTitle] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  // const Pension_associate = JSON.parse(sessionStorage.getItem('Response_to_Send_Pension'))
  const [to_Send, setTo_Send] = useState({});

  
  //for modal
  const [Modal, open, close] = useModal('root', {
    preventScroll: false,
    closeOnOverlayClick: true
  });

  
  const onChangeTitle = (e) => {
    const title = e.target.value;
    setTitle(title);
};
  // Call_Survey_Details form sessionStorage
  const Surveydetails = JSON.parse(sessionStorage.getItem('PensiondeductionInfo'));
  const Call_Survey_Details = Surveydetails.form;
  const to_Send_pension = JSON.parse(sessionStorage.getItem('Response_to_Send_Pension'))
  const Call_to_Send_pension = to_Send_pension?to_Send_pension.form:0;

  console.log(to_Send);


  useEffect(() => {
    // Call_Survey_Details form sessionStorage
    const Surveydetails = JSON.parse(sessionStorage.getItem('PensiondeductionInfo'));
    const Call_Survey_Details = Surveydetails;
    Set_Survey_Details_pension(Call_Survey_Details)
    const to_Send = JSON.parse(sessionStorage.getItem('Response_to_Send_Pension'))
    setTo_Send(to_Send);
    // console.log(Call_Survey_Details)
  }, [])

  
  // useUpdateEffect(() => { 
  //   return () => { 
  //   const to_Send = JSON.parse(sessionStorage.getItem('Response_to_Send_Pension'))
  //   setTo_Send(to_Send);
  //   }
  // });


 const  PensiondeductionResponse=(event)=>{
    event.preventDefault();

    const Response_Pension_Success = JSON.parse(sessionStorage.getItem('Response_Pension_Success'));

    const surveyId = Response_Pension_Success.survey_pk
    // console.log(survey.pk)
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;

   
    axios.get(`https://surveyng.cbn.gov.ng/api/survey/api/deduction-pension/detail/?survey_pk=${surveyId}`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            window.location = "/CompletePensiondeduction"

            if (res.data) {
                sessionStorage.setItem("PensiondeductionInfo", JSON.stringify(res.data));
            }

          })

  }

  const handleSubmit = (event) => {
    event.preventDefault();

    setMessage("");
    setSuccessful(false);

    setMessage("");
    setLoading(true);


    form.current.validateAll();

    //get the user from the sessionStorage
    const mytoken = JSON.parse(sessionStorage.getItem('_WannanGfsoCbnUserNe'));
    const token = mytoken.token;
    // const state= mytoken

    //get single survey details
    // const Surveydetails = JSON.parse(sessionStorage.getItem('PensiondeductionInfo'));

    //get the survey Pk  to send as require survey_pk
    // const SurveydetailsPk = Surveydetails.pk

    //Save all user responses of the survey  to sessionStorage
    const to_Send = JSON.parse(sessionStorage.getItem('Response_to_Send_Pension'))

    // returns an array of a given object's own enumerable property values

    const checkUser= JSON.parse(sessionStorage.getItem('_WannanGfsoCbnUserNe'));
    const checkUserpk=checkUser.profile
    //user state_pk
    const state_pk = checkUserpk.state
    // const lga_pk = checkUserpk.lga

    //Response Body to send to Mallam Ahmad
    const Responsebody =
    {
      title: title,
      survey_pk: to_Send.survey_pk,
      state_pk: state_pk,
      form: to_Send.form
    }
 
    console.log(Responsebody)
    if (checkBtn.current.context._errors.length === 0) {

      // console.log(Responsebody);
      axios.post("https://surveyng.cbn.gov.ng/api/survey/api/deduction-pension/response/add/", Responsebody,
        {
          headers:
          {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
          },
        })
        .then(res => {

          if (res.data) {
            sessionStorage.setItem("Response_Pension_Success", JSON.stringify(res.data));
          }
          setMessage(res.data.detail);
          setSuccessful(true);
          // window.location = "/AvailableSurvey"

        },
          (error) => {
            const resMessage =
              (error.response &&
                error.response.data.detail &&
                error.response.data.detail) ||
              error.survey_title ||
              error.toString();

            setMessage(resMessage);
            setSuccessful(false);
          }
        );
      // console.log(Get_Survey_Details_Pension)
    }
  }

  //this handle the change user typing
  const handleChange = (id, event) => {

    const newElements = { ...Get_Survey_Details_Pension }
      // console.log(newElements.form)

      //second array to loop through the field
      for (let j = 0; j < newElements.form.length; j++) {
        const field = newElements.form[j];
        // console.log(field)

        //check the pk for each field
        const { pk } = field;
        if (id === pk) {
          field['amount'] = event.target.value;
        }
        
        
      }
    //Save the result to the sessionStorage
    sessionStorage.setItem("Response_to_Send_Pension", JSON.stringify(Get_Survey_Details_Pension));
  }

  if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
    return (<Redirect to={'/login'} />)
  }
  const checkUser= JSON.parse(sessionStorage.getItem('_WannanGfsoCbnUserNe'));
  // console.log(to_Send?to_Send!=null && to_Send:"nothing to preview")
  // const  what_to_Send = to_Send? to_Send.form:0;
  return (

    <FormContext.Provider value={{ handleChange }}>
      <Header />
      <div className="content-wrapper" style={{ minHeight: 1602 }}>
        <Menu /><br />
        <section className="content">
          <div className="container-fluid">
            <div className="row">
              {/* <div className="card card-"> */}
              {/* left column */}
              
              {Surveydetails.form &&Surveydetails.form.length ? 

              <div className="col-md-12">
                <Form onSubmit={handleSubmit} ref={form}>
                  {!successful && (
                    <div>
                      <center><h1 class="mt-4 mb-2">{Surveydetails.category}</h1></center>
                      <center><h3 class="mt-4 mb-2">{Surveydetails.quarter}</h3></center>
                      <center><h4 class="mt-4 mb-2">{Surveydetails.year}</h4></center>
                      <center><h5 class="mt-4 mb-2">Make sure you are filling this survey from {checkUser.profile.state_title} state </h5></center>
                      {/* <CheckSurveyTypeResponse /> */}
                      <div className="form-group">
                              <label >Title</label>
                              
                              <input  type="text"
                                className="form-control"
                                value={title}
                                onChange={onChangeTitle}
                                required
                                placeholder="Enter Survey Title"  />  
                        </div>
                       <br/>
                      {Call_Survey_Details.map ((myValue, i) => (
                        <div className="card card-">
                          <div className="card-header">
                            <h3 className="card-title">{myValue.title}
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{myValue.LGA_ID}</h3>
                            <h6 className="">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{myValue.heading}</h6>
                          </div>
                          <div className="card-body">
                            <div className="row">
                             <FormElement key={i} field={myValue} />
                            </div>
                          </div>  
                        </div>
                      ))}
                      <div className="form-group">
                        {/* &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; */}
                          <button className="btn btn-primary btn-lg " disabled={loading}>
                          {loading && (
                            <span className="spinner-grow spinner-grow-sm"></span>
                          )}
                          <span>Submit</span>
                        </button>
                             <Modal> 
                            <div className="modal fade show" id="modal-lg" style={{display: 'block', paddingRight: 15}} aria-modal="true" role="dialog">
                           <div className="modal-dialog modal-lg">
                            <div className="modal-content">
                              <div className="modal-header">
                                <h4 className="modal-title">Preview Pension Deduction</h4>
                                <button type="button" className="close" onClick={close} data-dismiss="modal" aria-label="Close">
                                  <span aria-hidden="true"></span>
                                </button>
                              </div>
                              <div className="modal-body">
                                <p>
                                      <div className="table-container">
                                        <table className="table">
                                          <Link>
                                          {/* <i className="fas fa-sync-alt"  onClick={refreshPage}/> */}

                                          </Link>

                                          <thead>
                                              <th>STATE</th>

                                              <th> AMOUNT </th>
                                          </thead>
                                          <tbody className="table-body">
                                            {to_Send_pension ?Call_to_Send_pension  && Call_to_Send_pension.map((valueToSend, i) => (
                                              <tr>
                                                <td>
                                                  {valueToSend.title}
                                                </td>
                                                <td>
                                                  {valueToSend.amount}
                                                </td>
                                              </tr>
                                            )):"Nothing to Preview Please fill in the survey"}

                                          </tbody>
                                        </table>
                                      </div>
                                </p>
                              </div>
                              <div className="modal-footer justify-content-between">
                                <button type="button" className="btn btn-primary" onClick={close}>Close</button>
                                {/* <button type="button" className="">Save changes</button> */}
                              </div>
                            </div>
                            </div>
                            </div>
                          </Modal>
                        <Link className="btn btn-primary btn-lg float-right" to="/PensiondeductionSurveyList">
                          {loading && (
                            <span className="spinner-grow spinner-grow-sm"></span>
                          )}
                          <span>Next</span>
                        </Link>
                        
                      </div>
                    </div>
                  )}
                  <h3>
                    <center>
                      {message && (
                        <div className="form-group">
                          <div
                            className={
                              successful ? "alert alert-success" : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {message}
                            <br/>
                            <br/>

                            {successful &&
                                <Link
                                  className="btn btn-primary"
                                  onClick={PensiondeductionResponse}

                                >
                                  Respond Again
                                   </Link>
                              }
                          </div>
                        </div>
                      )}
                    </center>
                  </h3>

                  <CheckButton style={{ display: "none" }} ref={checkBtn} />
                </Form>
                  <center>
                  {/* <div>Please do not preview if nothing in the fields </div> */}
                    <button className="btn btn-success btn-block  "  onClick={open}>
                      {loading && (
                        <span className="spinner-grow spinner-grow-sm"></span>
                      )}
                      <span>Preview</span>
                    </button>
                  </center>
                       
              </div>
          : <center><h2>Only SG Responder Can Fill this Survey </h2></center> 
          }
            </div>
            {/* </div> */}
          </div>
        </section>
      </div>
      <Footer />
    </FormContext.Provider>
  );
}

export default CompletePensiondeduction;

