import axios from 'axios'
import React, { Component} from "react";
import { Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
// import ReactPaginate from 'react-paginate';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';



 export default class ResponseTable extends Component {

    constructor(props) {
        super(props);

        this.state = {
            ResponseTable:[]
           
        };
    }

  
   

    componentDidMount() {
             if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }

          const ax = axios.create({
            baseURL: '../data'
        })
        ax.get('responses.json')
            .then(res => {
                // if (res.data) {
                //     sessionStorage.setItem("ResponsesTable", JSON.stringify(res.data));
                // }
                const tdata = res.data;
                // console.log(res.data)   

                this.setState({
                    ResponseTable: tdata,
                })
            });
        // console.log(res.data)  

        

    }

    render() {

        if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }

        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                    <br />
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">

                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">Response Table</h3>
                                        </div>
                                        {/* /.card-header */}
                                        <div className="card-body">

                                            <table id="emp" class="table">
                                                <thead>
                                                    <tr>
                                                        {/* <th>Section</th>
                                                        <th>Item</th>                                                            <br />

                                                        <th> {this.state.ResponseQuater}&nbsp;{this.state.ResponseYear}</th>
                                                    */}
                                                    <th> meter number</th>
                                                    <th> Account number</th>
                                                    <th>  Customer Name</th>
                                                    <th>  Address</th>
                                                    <th>  Tarrif Code</th>
                                                    <th>  CSP</th>
                                                    <th>  Region</th>
                                                    <th>  Capturing Date</th>
                                                    <th>  Capturing Date</th>
                                                    <th>  Mobile</th>
                                                    <th>  ArrearsBalance</th>
                                                    
                                                                                                        

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.ResponseTable && this.state.ResponseTable.map(category => (

                                                        <tr>
                                                            <td>{category.MeterNo}</td>
                                                            <td>{category.AccountNo}</td>
                                                            <td>{category.CustomerName}</td>
                                                            <td>{category.Address}</td>
                                                            <td>{category.TariffCode}</td>
                                                            <td>{category.CSP}</td>
                                                            <td>{category.Region}</td>
                                                            <td>{category.CapturingDate}</td>
                                                            <td>{category.Mobile}</td>
                                                            <td>{category.ArrearsBalance}</td>

                                                            </tr>                                                    

                                                    ))}

                                                </tbody>

                                            </table>
                                            <div>
                                                <ReactHTMLTableToExcel
                                                    className="btn btn-info"
                                                    table="emp"
                                                    filename="ReportSurvey"
                                                    sheet="Sheet"
                                                    buttonText="Export excel" />
                                            </div>
                                            {/* */}

                                        </div>
                                    </div>
                                </div>


                            </div>


                        </div>


                    </section>
                </div>

                <Footer />
            </div>

        );
    }
}


