export default class RespondentList extends Component {

  constructor(props) {
    super(props);
   

    this.state = {
      
      Users:[],
      successful: false,
      message: "",
      loading: true,
      loadingModal:false,
      offset: 0,
      ListData:[],
      perPage: 5,
      currentPage: 0,




    };
  }

   //pagination responded State
   handlePageClick = (e) => {
    const selectedPage = e.selected;
    const offset = selectedPage * this.state.perPage;
    this.setState({
         currentPage: selectedPage,
         offset: offset
    }, () => {
         this.loadMoreData()
    });

};

//pagination responded State
loadMoreData() {
    const data = this.state.ListData;

    const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
    this.setState({
         pageCount: Math.ceil(data.length / this.state.perPage),
         mylist: slice
    })

}

  componentWillMount(){
    if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
      return (<Redirect to={'/login'} />)
    }

     const mytoken = JSON.parse(sessionStorage.getItem('_WannanGfsoCbnUserNe'));
     const token = mytoken.token;

     axios.get("https://surveyng.cbn.gov.ng/api/accounts/api/list/profile/?active=yes",
     {
       headers:
       {
         'Authorization': `Token ${token}`,
         'Access-Control-Allow-Origin': '*',
         'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
         'Access-Control-Allow-Credentials': true
       },
     })
     .then(res => {
       if (res.data) {
         sessionStorage.setItem("ActiveRespondent", JSON.stringify(res.data));
       }
       // Responses
       const data = res.data;
       const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage);

       this.setState({
         Users: res.data,
         loading:false,
         mylist: slice,
         ListData: data,
         pageCount:  Math.ceil(data.length / this.state.perPage),



     });
       // (res);
      //  console.log(res.data);

     })

  }

  ApproveResponder(users){

    const userId = users.pk
    // console.log(users.pk)
    const mytoken = AuthService.getCurrentUser();
    const token = mytoken.token;

    this.setState({
        loadingModal: true,
      });
    axios.get(`https://surveyng.cbn.gov.ng/api/accounts/api/details/profile/${userId}/`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            window.location = "/ApproveResponder"

            if (res.data) {
                sessionStorage.setItem("ReviewResponderList", JSON.stringify(res.data));
            }

            this.setState({
              ReviewResponder: res.data,
              // loadingModal: false,
            });

          })

  }

  
 
  
  render() {

    
    // console.log(list_of_lga);
    // const Users = JSON.parse(sessionStorage.getItem('Users'))




    return (
      <div>
        <Header />

        <div className="content-wrapper" >
        {this.state.loadingModal && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
          <Menu />
          <div className="col-md-12" >
            <br />
            {this.state.loading ? (
                                <>
                                    <center><Spinner animation="border" variant="success" /></center>
                                </>
                                ) : (
          <section className="content">
            <div className="row">
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">List of Respondents</h3>
                    <div className="card-tools">

                    </div>
                  </div>
                  {/* /.card-header */}

                  <div className="card-body table-responsive p-0">

                    <table className="table table-hover text-nowrap">
                      <thead>
                        <tr>
                          <th>Name</th>
                          <th>Phone</th>
                          <th>State</th>
                          <th>LGA</th>
                          <th>Registered on</th>
                          <th>Designation</th>
                          <th>Groups</th>
                          {/* <th>Branch</th> */}
                          <th>Action</th>
                          
                        </tr>
                      </thead>
                      {this.state.mylist && this.state.mylist.map(users => (

                        <tbody>
                          <tr>
                            <td>{users.username}&nbsp;</td>
                            <td>{users.phone_number}</td>
                            <td>{users.state_title}</td>
                            <td>{users.lga_title}</td>
                            <td>{users.created}</td>
                            <td>{users.designation_title}</td>
                            <td>{users?users.user_groups:'no group'}</td>
                            {/* <td>{users.branch_name}</td> */}
                                     <td className="">
                                          <Link 
                                          className="btn btn-lg btn-success "
                                           disabled={this.state.loading}
                                          onClick={this.ApproveResponder.bind(this, users)}

                                           >

                                               Approve
                                                   </Link>&nbsp;&nbsp;

                                                   
                                                  </td>

                          </tr>

                        </tbody>
                      ))}
                            <ReactPaginate
                              previousLabel={"prev"}
                              nextLabel={"next"}
                              breakLabel={"..."}
                              breakClassName={"break-me"}
                              pageCount={this.state.pageCount}
                              marginPagesDisplayed={2}
                              pageRangeDisplayed={5}
                              onPageChange={this.handlePageClick}
                              containerClassName={"pagination"}
                              subContainerClassName={"pages pagination"}
                              activeClassName={"active"} />
                    </table>
                  </div>


                  {/* /.card-body */}
                </div>
                {/* /.card */}
              </div>
            </div>
          </section>

          )}
        </div>
                               
        </div>
        <Footer />

        </div>


    );
  }
}