import React, { Component } from "react";
import { Redirect, Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import axios from "axios";
import Spinner from 'react-bootstrap/Spinner'
import Menu from "./Menu";
import authService from "../services/auth.service";
import Logo from '../cbn.png'
import ReactPaginate from 'react-paginate';


export default class Allsurveys extends Component {

    constructor(props) {
        super(props);
        this.state = {
            warningLoad: false,
            myloading: true,
            loading:false,
            message: "",
            Surveys: [],
            offsetSurveys:0,
            listOfSurveys: [],
            perPageSurveys: 10,
            currentPageSurveys: 0,  
        };
    }


     //pagination Survey
     handlePageClickSurvey = (e) => {
        const selectedPage = e.selected;
        const offsetSurveys = selectedPage * this.state.perPageSurveys;
        this.setState({
             currentPageSurveys: selectedPage,
             offsetSurveys: offsetSurveys
        }, () => {
             this.loadMoreDataSurvey()
        });
   };

   //pagination Survey
   loadMoreDataSurvey() {
        const data = this.state.listOfSurveys;

        const slice = data.slice(this.state.offsetSurveys, this.state.offsetSurveys + this.state.perPageSurveys)
        this.setState({
             pageCountSurveys: Math.ceil(data.length / this.state.perPageSurveys),
             mylistSurveys: slice
        })
   }

    componentDidMount() {
        if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/login'} />)
          }
        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://surveyng.cbn.gov.ng/api/survey/api/list/survey/?deployed=yes",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    sessionStorage.setItem("AllSurveys", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);

                const Surveydata = res.data ;
                const sliceSurvey =  Surveydata.slice(this.state.offsetSurveys, this.state.offsetSurveys + this.state.perPageSurveys);
      
                
                this.setState({
                    listOfSurveys: Surveydata,
                    pageCountSurveys: Math.ceil(Surveydata.length / this.state.perPageSurveys),
                    mylistSurveys: sliceSurvey,
                    Surveys: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })

    }

    EditSurvey(Surveys) {

        this.setState({
            loadingModal: true,
            loading:true
        });
        const mytoken = JSON.parse(sessionStorage.getItem('_WannanGfsoCbnUserNe'));
        const token = mytoken.token;

        const survey_pk = Surveys.pk;
        // console.log(survey_pk)



        let one = "https://surveyng.cbn.gov.ng/api/survey/api/list/response/";
        let two = `https://surveyng.cbn.gov.ng/api/survey/api/survey/analysis/`;

      const requestOne = axios.get(one,{
        headers:
        {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
        },
        params:{
            survey_pk:survey_pk
        }
    });
      const requestTwo = axios.get(two,{
        headers:
        {
            'Content-Type': 'application/json',
            'Authorization': `Token ${token}`,
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
            'Access-Control-Allow-Credentials': true
        },
        params:{
            survey_pk:survey_pk
        }
    });
      
      axios
        .all([requestOne, requestTwo, ])
        .then(
          axios.spread((...responses) => {
            // const responseOne = responses[0];
            sessionStorage.setItem("SurveyResponses", JSON.stringify(responses[0].data));
            sessionStorage.setItem("SurveyAnalysis", JSON.stringify(responses[1].data));
            // const responseTwo = responses[1];
            window.location = "/SurveyAnalysis"

            // use/access the results
            // console.log(responseOne, responseTwo);
          })
        )
        .catch(errors => {
          // react on errors.
          console.error(errors);
        });
      


            
    }

    render() {

        if (!sessionStorage.getItem('_WannanGfsoCbnUserNe')) {
            return (<Redirect to={'/'} />)
        }


        return (
            <div>
                <Header />
                <Menu />

                <section className="content-wrapper" >
                    <br />
                    {this.state.loading && (
                        <span className="">
                            <div className="overlay-wrapper">
                                <div className="overlay ">         
                      <center> <img src={Logo} height="50px" alt="" /></center><br />
                             <div className="text-bold pt-2"> &nbsp;&nbsp;Please wait ...</div></div>
                            </div>

                        </span>
                     )}
                    <div className="container-fluid">

                        {/* Default box */}
                        <div className="card">
                            <div className="card-header">

                                <h3 className="card-title">Surveys</h3>
                                <div className="card-tools">
                                    <button type="button" className="btn btn-tool" data-card-widget="collapse" title="Collapse">
                                        <i className="fas fa-minus" />
                                    </button>
                                    <button type="button" className="btn btn-tool" data-card-widget="remove" title="Remove">
                                        <i className="fas fa-times" />
                                    </button>
                                </div>
                            </div>
                            {this.state.myloading ? (
                                <>
                                    <center><Spinner animation="border" variant="success" /></center>
                                </>
                            ) : (
                                <div className="card-body p-0">

                                    <table className="table table-striped projects">
                                        <thead>
                                            <tr>
                                                
                                            <th style={{ width: '0%' }}>
                                                    Survey Name
                                               </th>
                                               {/* <th style={{ width: '0%' }}>
                                               Approved Templates 
                                               </th> */}
                                               <th style={{ width: '0%' }}>
                                                    Responses
                                               </th>
                                               <th style={{ width: '0%' }}>
                                                    Year
                                               </th>
                                               <th style={{ width: '0%' }}>
                                                      Quarter
                                                 </th> 
                                                 <th style={{ width: '0%' }}>
                                                  Type
                                                 </th>
                                              
                                                <th>
                                                    Survey Progress
                                                   </th>
                                                <th style={{ width: '0%' }} className="text-center">
                                                    Status
                                              </th>
                                                <th style={{ width: '0%' }}>
                                                    Action
                                                </th>
                                            </tr>
                                        </thead>
                                        {this.state.mylistSurveys.map(survey => (

                                            <tbody>
                                                <tr>
                                                    
                                                    <td>
                                                        <Link>
                                                            {survey.title}
                                                        </Link>
                                                        
                                                        <br />
                                                        <small>
                                                            Created   {survey.created}

                                                        </small>
                                                    </td>
                                                    {/* <td>
                                                        <Link>
                                                            {survey.template_title}
                                                        </Link>
                                                        
                                                        <br />
                                                        <small>
                                                            Created   {survey.approved_template_timestamp}

                                                        </small>
                                                    </td> */}
                                                    <td>
                                                        <Link>
                                                            {survey.get_response_count}
                                                        </Link>

                                                    </td>
                                                    <td>
                                                        <Link>
                                                            {survey.year}
                                                        </Link>

                                                    </td>
                                                    <td>
                                                        <Link>
                                                            {survey.quarter}
                                                        </Link>

                                                    </td>   
                                                    <td>
                                                        <Link>
                                                            {survey.survey_type}
                                                        </Link>

                                                    </td>
                                                    


                                                    <td className="project_progress">
                                                        <div className="progress progress-sm">
                                                            <div className="progress-bar bg-green" role="progressbar" aria-valuenow={25} aria-valuemin={0} aria-valuemax={50} style={{ width: `${survey.progress+'%'} `}}>
                                                            </div>
                                                        </div>
                                                        <small>
                                                            {survey.progress+'%'}
                                                    </small>
                                                    </td>

                                                    <td className="project-state">
                                                        {survey.status==='open'?
                                                        <span className="badge badge-success">{survey.status}</span>
                                                        :
                                                        <span className="badge badge-danger">{survey.status}</span>

                                                        }
                                                    </td>
                                                    <td className="project-actions text-right">
                                                    <Link
                                                         className="btn btn-primary btn-sm"
                                                          onClick={this.EditSurvey.bind(this, survey)}

                                                          >
                                                     View
                                                    </Link>&nbsp;&nbsp;
 
                                                  </td>

                                                </tr>

                                            </tbody>
                                        ))}
                                            <ReactPaginate
                                                previousLabel={"prev"}
                                                nextLabel={"next"}
                                                breakLabel={"..."}
                                                breakClassName={"break-me"}
                                                pageCount={this.state.pageCountSurveys}
                                                marginPagesDisplayed={2}
                                                pageRangeDisplayed={5}
                                                onPageChange={this.handlePageClickSurvey}
                                                containerClassName={"pagination"}
                                                subContainerClassName={"pages pagination"}
                                                activeClassName={"active"} />
                                    </table>
                                </div>

                            )}

                            {/* /.card-body */}
                        </div>
                        {/* /.card */}
                    </div>

                </section>


                <Footer />

            </div>
        );
    }
}